import { request, requestDownload } from 'utils';

// request.prefix = '/api/categories';
const prefix = '/api/students';

export const getStudentsForAssociationMem = ({
  groupId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: `/Group/${groupId}/member`,
    method: 'GET',
    params: {
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: { groupId },
  });

export const updatePoliticalDate = ({ id, politicalDate }) =>
  request({
    prefix,
    url: `/Student/${id}/UpdatePoliticalDate`,
    params: {},
    data: { ids: [id], politicalDate },
    method: 'PUT',
  });

export const exportPoliticStudents = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  studentCode,
  studentFullName,
}) =>
  requestDownload({
    defaultFileName: 'fileDownload.xlsx',
    prefix,
    url: `/Student/ExportPoliticStudents`,
    method: 'POST',
    data: { trainTypeId, facultyId, courseId, studyFieldId, classId, studentCode, studentFullName },
  });



